* {
    box-sizing: border-box;

    outline: none;
}

html,
body {
    width: 100%;
    height: 100%;
}

html {
    font-size: 10px;
}

body {
    position: relative;

    overflow-x: hidden;

    min-width: 320px;
    margin: 0;
    padding: 0;

    font-family: 'Open Sans', sans-serif; 
    font-size: 1.6rem;
}

header,
section,
footer {
    position: relative;

    overflow-x: hidden;
}

nav,
ul {
    margin: 0; 

    list-style-type: none;
}

a,
a:focus,
a:link,
a:hover,
a:visited {
    color: inherit; 

    text-decoration: none;
}

input:focus::-webkit-input-placeholder,
textarea:focus::-webkit-input-placeholder {
    color: transparent;
}

input::-ms-clear {
    display: none;
}

.container {
    max-width: 1415px;
    height: 100%;
    margin: 0 auto;
}

.btn {
    cursor: pointer;
}

.main {
    height: 100%;
}
.main__wrapper {
    display: flex;

    height: 100%; 

    justify-content: space-between;
}

.sidebar {
    background-color: #303841;

    flex-basis: 319px;
}

.panel {
    overflow: hidden;
}
.panel__logo {
    margin-top: 40px;
    margin-left: 26px; 

    font-family: 'Myriad Pro', sans-serif;
    font-size: 2.8rem;

    color: #ffc12d;

    letter-spacing: .7px;
    text-transform: uppercase;
}
.panel__container {
    margin-top: 45px;
}
.panel__link {
    position: relative;

    padding: 17px 0;
    padding-left: 47px; 

    font-size: 1.7rem;
    font-weight: 300;

    color: #fff;
    border-bottom: 2px solid rgba(255, 255, 255, .07);
}
.panel__link::before {
    position: absolute;
    top: 45%;
    left: 8px;

    font-family: 'FontAwesome';
    font-size: 2.2rem; 

    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.panel__item {
    position: relative;

    display: block;

    padding: 0 20px; 

    cursor: pointer;
}
.panel__item_dashboard .panel__link::before {
    top: 42%; 

    width: 23px;
    height: 18px;

    content: '\f0e4';
}
.panel__item_reports .panel__link::before {
    width: 22px;
    height: 21px; 

    content: '\f200';
}
.panel__item_customers .panel__link::before {
    width: 24px;
    height: 19px; 

    content: '\f0b1';
}
.panel__item_calendar .panel__link::before {
    width: 21px;
    height: 22px; 

    content: '\f073';
}
.panel__item_active {
    background-color: #442a9d;
}
.panel__item_active .panel__link {
    border-color: transparent;
}
.panel__item_active::before {
    position: absolute;
    top: -4px;
    left: 0;

    width: 100%;
    height: 4px;

    background-color: #303841; 

    content: '';
}
.panel__item_active::after {
    position: absolute;
    top: 0;
    left: 0;

    width: 5px;
    height: 100%;

    background-color: #ffc12d; 

    content: '';
}

.content {
    flex-grow: 1;
}
.content__wrapper {
    padding-left: 34px;
}

.header {
    display: flex;

    padding: 15px 34px; 

    border-bottom: 2px solid rgba(237, 237, 237, .4);

    justify-content: space-between;
}
.header__container {
    display: flex;

    justify-content: space-between;
    align-items: center;
}

.breadcrumbs {
    display: flex;

    margin-left: 2px; 

    justify-content: flex-start;
    align-items: center;
}
.breadcrumbs__item {
    position: relative; 

    margin-right: 51px;

    font-weight: 700;

    color: #4f4f4f;

    cursor: pointer;
}
.breadcrumbs__item::after {
    position: absolute;
    top: calc(50% - 7px);
    right: -32px;

    width: 10px;
    height: 14px;

    font-family: 'FontAwesome';
    font-size: 1.5rem;
    font-weight: 400;

    color: #4f4f4f;

    content: '\f054';
    cursor: default;
}
.breadcrumbs__item_current {
    padding-right: 0; 

    color: #442a9d;
}
.breadcrumbs__item_current::after {
    display: none;
}
.breadcrumbs__link {
    white-space: nowrap;
}

.search {
    position: relative;

    width: 285px;
}
.search__input {
    width: 100%;
    height: 38px;
    padding: 0 50px 0 21px;

    color: #4f4f4f; 
    border: 1px solid #ededed;
    border-radius: 4px;
}
.search__input::-webkit-input-placeholder {
    font-size: 1.2rem;

    color: #999;

    letter-spacing: .24px; 
    text-transform: uppercase;
}
.search__input:-moz-placeholder {
    font-size: 1.2rem;

    color: #999;

    letter-spacing: .24px; 
    text-transform: uppercase;
}
.search__input::-moz-placeholder {
    font-size: 1.2rem;

    color: #999;

    letter-spacing: .24px; 
    text-transform: uppercase;
}
.search__input:-ms-input-placeholder {
    font-size: 1.2rem;

    color: #999;

    letter-spacing: .24px; 
    text-transform: uppercase;
}
.search__btn {
    position: absolute;
    top: 50%;
    right: 13px;

    width: 27px;
    height: 27px;

    font-family: 'FontAwesome';

    border: none;
    background-color: transparent;

    cursor: pointer; 
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
}
.search__btn::before {
    position: absolute;
    top: calc(50% - 8.5px);
    right: calc(50% - 8.5px);

    width: 17px;
    height: 17px;

    font-size: 1.7rem; 

    color: #999;

    content: '\f002';
}

.notice {
    position: relative;

    width: 29px;
    height: 25px;
    margin: 0 34px 0 42px; 

    font-family: 'FontAwesome';

    cursor: pointer;
}
.notice::before {
    position: absolute;
    top: 0;
    left: 0;

    width: 19px;
    height: 20px;

    font-size: 2rem; 

    color: #ededed;

    content: '\f0f3';
}
.notice__counter {
    position: absolute;
    top: 0;
    right: 0;

    width: 15px;
    height: 18px;

    font-size: 1.2rem;
    font-weight: 700;
    line-height: 18px; 

    color: #f4f4f4;
    border-radius: 50%;
    background-color: #f14e95;

    text-align: center;
}

.account {
    display: flex;

    justify-content: space-between;
    align-items: center;
}
.account__avatar {
    margin-right: 12px;
}
.account__name {
    font-weight: 700;
    line-height: 29.64px;

    color: #4f4f4f;

    white-space: nowrap;
}

.profile {
    display: flex;

    margin-top: 53px; 

    justify-content: flex-start;
    align-items: flex-start;
}
.profile__avatar {
    margin-top: 6px;
}
.profile__container {
    margin-left: 40px;

    flex-grow: 1;
}
.profile__box {
    display: flex;

    font-weight: 700;

    text-transform: uppercase; 

    align-items: center;
}
.profile__name {
    margin-right: 19px; 

    font-size: 2rem;

    color: #4f4f4f;
}
.profile__status {
    padding: 1px 8px; 

    font-size: 1.1rem;

    color: #fff;
    border-radius: 4px;
    background-color: #ffc12d;

    letter-spacing: .45px;
}
.profile__position {
    margin-top: 5px; 

    font-size: 1.5rem;
    font-weight: 700;

    color: #999;
}
.profile__contact {
    margin-top: 16px;
    padding-top: 26px; 

    border-top: 2px solid #ededed;
}
.profile__btn_create-quote {
    height: 36px;
    margin-right: 35px; 
    padding: 0 28px;

    font-weight: 700;
    line-height: 36px;

    color: #ffc12d;
    border-radius: 4px;
    background-color: #303841;

    letter-spacing: .28px;
    text-transform: uppercase;
}

.contact {
    display: flex;

    justify-content: space-between;
}
.contact__item {
    font-weight: 700;
}
.contact__title {
    font-size: 1.2rem;

    color: #999;

    letter-spacing: .5px; 
    text-transform: uppercase;
}
.contact__value {
    margin-top: 4px; 

    font-size: 1.5rem;

    color: #442a9d;
}

.tabs {
    display: flex;

    margin-top: 76px;
}
.tabs__link {
    margin-right: 25px; 
    padding: 10px 16px 25px;

    font-size: 1.4rem;
    font-weight: 700;

    color: #303841;

    cursor: pointer;
    text-transform: uppercase;
}
.tabs__link_active {
    position: relative;
}
.tabs__link_active::after {
    position: absolute;
    bottom: 0;
    left: 0;

    width: 100%;
    height: 6px;

    border-radius: 3px; 
    background-color: #442a9d;

    content: '';
}

