* {
  box-sizing: border-box;
  outline: none; }

html, body {
  width: 100%;
  height: 100%; }

html {
  font-size: 10px; }

body {
  margin: 0;
  padding: 0;
  min-width: 320px;
  position: relative;
  font-size: 1.6rem;
  overflow-x: hidden;
  font-family: "Open Sans", sans-serif; }

header, section, footer {
  position: relative;
  overflow-x: hidden; }

nav, ul {
  list-style-type: none;
  margin: 0; }

a, a:focus, a:link, a:hover, a:visited {
  text-decoration: none;
  color: inherit; }

input:focus::-webkit-input-placeholder, textarea:focus::-webkit-input-placeholder {
  color: transparent; }

input::-ms-clear {
  display: none; }

.container {
  max-width: 1415px;
  height: 100%;
  margin: 0 auto; }

.btn {
  cursor: pointer; }

.main {
  height: 100%; }
  .main__wrapper {
    display: flex;
    justify-content: space-between;
    height: 100%; }

.sidebar {
  background-color: #303841;
  flex-basis: 319px; }

.panel {
  overflow: hidden; }
  .panel__logo {
    font-family: "Myriad Pro", sans-serif;
    color: #ffc12d;
    letter-spacing: 0.7px;
    font-size: 2.8rem;
    text-transform: uppercase;
    margin-top: 40px;
    margin-left: 26px; }
  .panel__container {
    margin-top: 45px; }
  .panel__link {
    position: relative;
    color: #fff;
    font-size: 1.7rem;
    font-weight: 300;
    border-bottom: 2px solid rgba(255, 255, 255, 0.07);
    padding: 17px 0;
    padding-left: 47px; }
    .panel__link::before {
      position: absolute;
      top: 45%;
      transform: translateY(-50%);
      left: 8px;
      font-family: "FontAwesome";
      font-size: 2.2rem; }
  .panel__item {
    display: block;
    cursor: pointer;
    position: relative;
    padding: 0 20px; }
    .panel__item_dashboard .panel__link::before {
      content: '\f0e4';
      width: 23px;
      height: 18px;
      top: 42%; }
    .panel__item_reports .panel__link::before {
      content: '\f200';
      width: 22px;
      height: 21px; }
    .panel__item_customers .panel__link::before {
      content: '\f0b1';
      width: 24px;
      height: 19px; }
    .panel__item_calendar .panel__link::before {
      content: '\f073';
      width: 21px;
      height: 22px; }
    .panel__item_active {
      background-color: #442a9d; }
      .panel__item_active .panel__link {
        border-color: transparent; }
      .panel__item_active::before {
        content: '';
        position: absolute;
        top: -4px;
        left: 0;
        width: 100%;
        height: 4px;
        background-color: #303841; }
      .panel__item_active::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 5px;
        height: 100%;
        background-color: #ffc12d; }

.content {
  flex-grow: 1; }
  .content__wrapper {
    padding-left: 34px; }

.header {
  display: flex;
  justify-content: space-between;
  border-bottom: 2px solid rgba(237, 237, 237, 0.4);
  padding: 15px 34px; }
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.breadcrumbs {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 2px; }
  .breadcrumbs__item {
    color: #4f4f4f;
    font-weight: 700;
    cursor: pointer;
    margin-right: 51px;
    position: relative; }
    .breadcrumbs__item::after {
      content: '\f054';
      position: absolute;
      top: calc(50% - 7px);
      right: -32px;
      width: 10px;
      height: 14px;
      font-size: 1.5rem;
      color: #4f4f4f;
      font-weight: 400;
      font-family: "FontAwesome";
      cursor: default; }
    .breadcrumbs__item_current {
      color: #442a9d;
      padding-right: 0; }
      .breadcrumbs__item_current::after {
        display: none; }
  .breadcrumbs__link {
    white-space: nowrap; }

.search {
  position: relative;
  width: 285px; }
  .search__input {
    width: 100%;
    height: 38px;
    border-radius: 4px;
    border: 1px solid #ededed;
    padding: 0 50px 0 21px;
    color: #4f4f4f; }
    .search__input::-webkit-input-placeholder {
      color: #999999;
      text-transform: uppercase;
      font-size: 1.2rem;
      letter-spacing: 0.24px; }
    .search__input:-moz-placeholder {
      color: #999999;
      text-transform: uppercase;
      font-size: 1.2rem;
      letter-spacing: 0.24px; }
    .search__input::-moz-placeholder {
      color: #999999;
      text-transform: uppercase;
      font-size: 1.2rem;
      letter-spacing: 0.24px; }
    .search__input:-ms-input-placeholder {
      color: #999999;
      text-transform: uppercase;
      font-size: 1.2rem;
      letter-spacing: 0.24px; }
  .search__btn {
    font-family: "FontAwesome";
    width: 27px;
    height: 27px;
    border: none;
    background-color: transparent;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: 13px;
    cursor: pointer; }
    .search__btn::before {
      content: '\f002';
      position: absolute;
      top: calc(50% - 8.5px);
      right: calc(50% - 8.5px);
      width: 17px;
      height: 17px;
      color: #999999;
      font-size: 1.7rem; }

.notice {
  position: relative;
  font-family: "FontAwesome";
  width: 29px;
  height: 25px;
  cursor: pointer;
  margin: 0 34px 0 42px; }
  .notice::before {
    content: '\f0f3';
    position: absolute;
    top: 0;
    left: 0;
    width: 19px;
    height: 20px;
    color: #ededed;
    font-size: 2rem; }
  .notice__counter {
    position: absolute;
    top: 0;
    right: 0;
    width: 15px;
    height: 18px;
    border-radius: 50%;
    background-color: #f14e95;
    font-size: 1.2rem;
    font-weight: 700;
    color: #f4f4f4;
    text-align: center;
    line-height: 18px; }

.account {
  display: flex;
  justify-content: space-between;
  align-items: center; }
  .account__avatar {
    margin-right: 12px; }
  .account__name {
