    letter-spacing: 0.45px;
    padding: 1px 8px; }
  .profile__position {
    font-weight: 700;
    color: #999999;
    font-size: 1.5rem;
    margin-top: 5px; }
  .profile__contact {
    border-top: 2px solid #ededed;
    margin-top: 16px;
    padding-top: 26px; }
  .profile__btn_create-quote {
    color: #ffc12d;
    font-weight: 700;
    letter-spacing: 0.28px;
    background-color: #303841;
    border-radius: 4px;
    text-transform: uppercase;
    padding: 0 28px;
    height: 36px;
    line-height: 36px;
    margin-right: 35px; }

.contact {
  display: flex;
  justify-content: space-between; }
  .contact__item {
    font-weight: 700; }
  .contact__title {
    color: #999999;
    font-size: 1.2rem;
    text-transform: uppercase;
    letter-spacing: 0.5px; }
  .contact__value {
    color: #442a9d;
    font-size: 1.5rem;
    margin-top: 4px; }

.tabs {
  display: flex;
  margin-top: 76px; }
  .tabs__link {
    color: #303841;
    font-size: 1.4rem;
    text-transform: uppercase;
    font-weight: 700;
    padding: 10px 16px 25px;
    cursor: pointer;
    margin-right: 25px; }
    .tabs__link_active {
      position: relative; }
      .tabs__link_active::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 6px;
        background-color: #442a9d;
        border-radius: 3px; }

